import "../../../../common/extensions/global"
import tinyMceLoader from "../../../../common/TinyMceHelper";
import * as tinymce from "tinymce";
import "../../../scss/components/commentZ.scss";

interface ICommentZ {
    Id?: string | number;
    Img?: string;
    Create?: string;
    NickName: string;
    Content: string;
    Email: string;
    IsSecret?: number;

    ReplyId?: number | string;
    ReplyName?: string;
    ReplyBody?: ICommentZ[];
}

interface ISetting {
    selector: string,
    editorConfig: any,

    queryComments: (comment: CommentZ, page?: number, pageCommentSize?: number) => void;
    paging: boolean,
    pageCommentSize?: number;
    commentSubmit: (comment: ICommentZ, callBack: (newComment: any) => void) => void;
}

class CommentZ {

    constructor(setting: ISetting) {
        this.setting = setting;
        this.page = 1;
        if (this.setting.paging) {
            if (!this.setting.pageCommentSize) {
                this.setting.pageCommentSize = 10;
            }
        }
        this.$showReplyBox = null;
    }
    page: number;
    setting: ISetting;
    $selectorDom: any = null;
    $showReplyBox:any= null;

    init() {
        this.$selectorDom = $(this.setting!.selector);
        let commentHtml =
            `
<div class="cz-container">
    <div class="row">
        <textarea id="cz-editor"></textarea>
    </div>
    <div class="row cz-tool" style="margin-top: 2rem;">
        <div class="col-md-5 col-sm-4 col-12 form-group" style="line-height: 34px;">
            <input id="cz-nickName" type="text" required="required" class="form-control" placeholder="昵称(限长:8)" maxlength="8" />
        </div>
        <div class="col-md-5 col-sm-4 col-12 form-group" style="line-height: 34px;">
            <input id="cz-email" type="email" required="required" class="form-control" placeholder="邮箱" />
        </div>

        <div class="col-md-2 col-sm-4 col-12 form-group">
            <div class="pull-right">
                <button class="btn btn-primary pull-right" id="cz-submit">评论</button>
            </div>
            <div class="clearfix"></div>
        </div>
    </div>
    <div id="cz-comments" class="mt-4"></div>
</div>`;
        this.$selectorDom.html(commentHtml);
        this.queryComments();
        
        tinyMceLoader.init("#cz-editor", "basic", "240");
        let that = this;

        $("#cz-submit").click((e) => {
            e.preventDefault();
            let content:string = tinymce.get('cz-editor').getContent();
            let nickName: string = <string>$("#cz-nickName", that.$selectorDom).val();
            let email: string = <string>$("#cz-email", that.$selectorDom).val();
            let comment: ICommentZ = {
                NickName: nickName,
                Content: content,
                Email: email,
                Create :CommentZ.getNowDateFormat()
            }
            that.submitComment(comment, (newComment: ICommentZ) => {
                //TODO 更新楼层
                let createComment = that.createComment(newComment, '新');
                $("#cz-comments", that.$selectorDom).prepend(createComment);
                this.clear();
                
            });
        });

    }

    clear() {
        tinymce.get('cz-editor').setContent("");
        $("#cz-nickName", this.$selectorDom).val("");
        $("#cz-email", this.$selectorDom).val("");
    }

    parseData(data: any) {
        this.createComments(data);
    }

    createReplyComment(replyObj: ICommentZ) {
        return `<div class="reply" id='cz-${replyObj.Id}' data-id="${replyObj.Id}">
            <div class="cz-reply-name"><a href="javascript:void(0)">${replyObj.NickName}</a>:<a href="#cz-${replyObj.ReplyId}">@${replyObj.ReplyName}</a></div>
            <p>${replyObj.Content}</p>
            <div class='reply-footer'><span>${replyObj.Create!.iso8601ToDate().format("yyyy-MM-dd HH:mm:ss")}</span><span class="reply-list-btn">回复</span></div>
        </div>`;
    }

    createComment(comment: ICommentZ, index: number|string) {
        let replyComments = '';
        if (comment.ReplyBody !== null && comment.ReplyBody!.length > 0) {
            let that = this;
            replyComments = comment.ReplyBody!.map((item: any) => {
                return that.createReplyComment(item);
            }).join(' ');
        }
        let commentHtml = `<div class="comment-info"  data-id="${comment.Id}">
            <div class="comment-left">
                <header class='hidden-sm hidden-xs'>
                    <span><b>#${index}楼</b></span>
                </header>
            </div>
            <div class="comment-right" id='cz-${comment.Id}'>
                <div class='cz-uname'>${comment.NickName}</div>
                <div class="comment-content-header"><span><i class="fa fa-clock-o"></i> ${comment.Create!.iso8601ToDate().format("yyyy-MM-dd HH:mm:ss")}</span></div>
                <div class="content">
                    ${comment.Content}
                </div>
                <div class="comment-content-footer">
                    <div class="row">                        
                        <div class="col-12"><span class="reply-btn">回复</span></div>
                    </div>
                </div>
                <div class="reply-list">${replyComments}</div>
            </div>
        </div>`;
        return commentHtml;
    }

    createComments(comments: Array<ICommentZ>) {
        let $czComments = $("#cz-comments", this.$selectorDom);

        let that = this;
        let commentsContent = comments.map((item: ICommentZ, index: number) => {
            return that.createComment(item, index+1);
        }).join(" ");

        $czComments.html(commentsContent);

        $czComments.find(".reply-btn").click(e => {
            that.replyClick(e.target);
        });
        $czComments.find(".reply-list-btn").click(e => {
            that.replyClick(e.target);
        });
    }

    replyClick(element: any) {
        let el = $(element);
        let $replyBoxContainer = el.parent().parent();
        let $newReplyBox = $replyBoxContainer.children(".replybox");

        if ($newReplyBox.length > 0) {
            if (this.$showReplyBox[0].isEqualNode($newReplyBox[0])) {
                this.$showReplyBox.remove();
                this.$showReplyBox = null;
                tinymce.get("cz-reply-editor").remove();
                return;
            }
        }
        else {
            if (this.$showReplyBox !== null) {
                this.$showReplyBox.remove();
                this.$showReplyBox = null;
                tinymce.get("cz-reply-editor").remove();

            }
        }

        let replyHtml = 
            `<div class='replybox'>
                <textarea id='cz-reply-editor' cols='80' rows='50' placeholder='来说几句吧......' class='mytextarea'></textarea>
                <div class="row cz-tool">
                    <div class="col-md-5 col-sm-4 col-12 form-group" style="line-height: 34px;">
                        <input id="nickName2" type="text" required="required" class="form-control" placeholder="昵称(限长:8)" maxlength="8" data-cip-id="cz-nickName">
                    </div>
                    <div class="col-md-5 col-sm-4 col-12 form-group" style="line-height: 34px;">
                        <input id="ema2" type="email" required="required" class="form-control" placeholder="邮箱" data-cip-id="cz-email">
                    </div>

                    <div class="col-md-2 col-sm-4 col-12 form-group send-container">
                        <span class='send'>发送</span>
                    </div>
                </div>
            </div>`;

        var $replyHtml = $(replyHtml);
        this.$showReplyBox = $replyHtml;

        console.log("submit");
        $replyHtml.find(".send").click(e => {
            let $this = $(e.target);

            var content = tinymce.get("cz-reply-editor").getContent();//$("#cz-reply-editor").val();

            var parentEl = $this.parents('.comment-right');
            var nickName = $("#nickName2").val();
            var ema = <string>$("#ema2").val();


            var id: any, beReplyName: any;
            console.log(el[0]);
            if (el.hasClass("reply-list-btn")) {
                let $repltParent = el.parents(".reply");
                id = $repltParent.data("id");
                beReplyName = $repltParent
                    .children(".cz-reply-name")
                    .children("a").eq(0).text();
            } else {
                id = el.parents(".comment-info").data("id");
                beReplyName = el
                    .parents(".comment-right")
                    .children(".cz-uname")
                    .text();
            }
            console.log(beReplyName);

            var obj: ICommentZ = {
                ReplyId: id,
                Img: "N",
                NickName: <string>nickName,
                Content: <string>content,
                Email: ema
            };

            this.submitComment(obj, (newComment: ICommentZ) => {
                newComment.ReplyName = beReplyName;
                console.log(this);
                var replyString = this.createReplyComment(newComment);

                $(".replybox").remove();
                var $replyString = $(replyString);
                $replyString.find(".reply-list-btn:last").click(() => {
                    alert("不能回复自己");
                    return;
                });
                parentEl.find(".reply-list").append($replyString);
            });
        });

        //评论回复事件
        $replyBoxContainer.append($replyHtml);
        tinyMceLoader.init("#cz-reply-editor", "basic", 200);

    }

    submitComment(comment: ICommentZ, callBack?: (comment:ICommentZ)=> void) {
        if (!comment.Content) {
            alert("回复内容不能为空！");
            return;
        }
        if (!comment.NickName) {
            alert("昵称不能为空！");
            return;
        }
        if (!comment.Email) {
            alert("邮箱不能为空！");
            return;

        }
        if (!comment.Email.isEmail()) {
            alert("邮箱格式不正确！");
            return;
        }
        if (!comment.Create) {
            comment.Create = CommentZ.getNowDateFormat();
        }
        

        this.setting.commentSubmit(comment,
            (newComment: ICommentZ) => {
                typeof callBack === "function" && callBack(newComment);
            });
    }

    queryComments(page: number = 1) {
        if (this.setting.paging) {
            this.page = page;
            this.setting.queryComments(this, page, this.setting.pageCommentSize);
        } else {
            this.setting.queryComments(this);
        }
    }

    /**
     * 
     * Start 时间处理
     */

    static getNowDateFormat() {
        var nowDate = new Date();
        var year = nowDate.getFullYear();
        var month = CommentZ.filterNum(nowDate.getMonth() + 1);
        var day = CommentZ.filterNum(nowDate.getDate());
        var hours = CommentZ.filterNum(nowDate.getHours());
        var min = CommentZ.filterNum(nowDate.getMinutes());
        var seconds = CommentZ.filterNum(nowDate.getSeconds());
        return year + "-" + month + "-" + day + " " + hours + ":" + min + ":" + seconds;
    }
    static filterNum(num: any) {
        if (num < 10) {
            return "0" + num;
        } else {
            return num;
        }
    }

    /**
    *
    * End 时间处理
    */
}

export { CommentZ, ICommentZ, ISetting };
export default CommentZ;