import { CommentZ, ISetting } from "../components/comment/CommentZ";
import Global from "../common/global";
import tinyMceConfig from "../../../common/tinyMceConfig";

(()=>{

    //评论操作
    let commentsApi = "/api/Home/SiteMessages";
    let submitCommentApi = "/api/Home/SubmitSiteMessage";
    const queryEssayComments = (commentZ: CommentZ) => {
        Global.showLoading();
        $.ajax({
            type: "get",
            url: commentsApi,
            success: (msg: any) => {
                if (msg.Code === 0) {
                    commentZ.parseData(msg.Data);
                }
            },
            complete: () => {
                Global.hideLoading();
            }
        })
    }

    const submitComment = (comment: any, callBack: (newComment: any) => void) => {
        let formData = new FormData();
        for (var prop in comment) {
            if (comment.hasOwnProperty(prop)) {
                formData.append(prop, comment[prop]);
            }
        }
        Global.showLoading("操作中...");
        $.ajax({
            url: submitCommentApi,
            type: 'post',
            processData: false,
            contentType: false,
            data: <any>formData,
            success: (msg: any) => {
                if (msg.Code === 0) {
                    typeof callBack === "function" && callBack(msg.Data);
                }
            },
            complete() {
                Global.hideLoading();
                Global.alert("留言成功");
            }
        });
    }

    let commentZSetting: ISetting = {
        selector: "#commentBox",
        editorConfig: tinyMceConfig["basic"],
        queryComments: queryEssayComments,
        commentSubmit: submitComment,
        paging:false
    }

    let commentZ = new CommentZ(commentZSetting);
    commentZ.init();
})();