export { }

declare global {
    interface String {
        isEmail(): boolean;
        iso8601ToLocal(): string;
        iso8601ToDate():Date;
    }
    interface Date {
        format(fmt: string): string;
    }

    interface JQueryStatic {
        cookie(key: string): any;
        cookie(key: string, value: string): void;
    }
}




String.prototype.isEmail = function (this: string): boolean {
    var email = this.replace(" ", "");
    var pattern = /^([\w-.]+)@([\w_-]+\.)+([a-zA-Z0-9]+)/i;
    var flag = pattern.test(email);
    if (flag) {
        return true;
    } else {
        return false;
    }
}

String.prototype.iso8601ToLocal = function () {
    let str = this.replace(/T/g, ' ');
    return str;
}

Date.prototype.format = function (fmt) {
    var o: any = {
        "M+": this.getMonth() + 1, //月份 
        "d+": this.getDate(), //日 
        "H+": this.getHours(), //小时 
        "m+": this.getMinutes(), //分 
        "s+": this.getSeconds(), //秒 
        "q+": Math.floor((this.getMonth() + 3) / 3), //季度 
        "S": this.getMilliseconds() //毫秒 
    };
    if (/(y+)/.test(fmt))
        fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o) {
        if (o.hasOwnProperty(k)) {
            if (new RegExp("(" + k + ")").test(fmt)) {
                let okValue = o[k];
                fmt = fmt.replace(RegExp.$1,
                    (RegExp.$1.length === 1)
                        ? okValue
                        : (("00" + okValue).substr(("" + okValue).length)));
            }
        }
    }

    return fmt;
}

String.prototype.iso8601ToDate = function () {
    return new Date((this as string));
}