interface IGlobal {
    showLoading: (text?: string) => void;
    hideLoading: () => void;
    alert: (text:string, obj?: any)=>void;
}

const Global: IGlobal = {

    showLoading: (text = null) => {
        let showText = "加载中...";
        if (text) {
            showText = text;
        }

        $("#gLoading .loading-text").html(showText);
        $("#gLoading").css("display", "block");
    },

    hideLoading: () => {
        $("#gLoading").css("display", "none");
    },

    alert: (text, obj = null) => {
        let defaultObj = {
            time: 2000,
            title:"提示"
        }

        if (obj) {
            if (typeof obj === "function") {
                //callback
                (<any>window).layer.alert(text, defaultObj, obj);
            } else {
                $.extend(obj, defaultObj);
                (<any>window).layer.alert(text, obj);
            }
        } else {
            (<any>window).layer.alert(text, defaultObj);
        }
    }
}

export default Global;