import * as tinymce from 'tinymce';
// @ts-ignore TS7016
import * as tinyMceConfig from "./tinyMceConfig.js";

const cdn = "https://cdn.jsdelivr.net/gh/zxyao145/JsDelivr@202206237/zxyao.net/assets";

interface ITinyMceLoader {
    init: (selector: string, tinymceType?: string,
        height?: any | null, width?: any | null, otherConfig?: any | null, onReady?: (() => void) | null) => void;
    setContent: (selector: string, content: string) => void;

    getContent: (selector: string) => string | null;
}

const primseHighLight = () => {
    let allUeditorPreCode = $("#tinymce");
    allUeditorPreCode.each(function () {
        var $this = $(this);
        var classVal = $this.attr('class');
        var classArr = classVal!.split(';');
        classArr = classArr[0].split(':');
        var lanClass = 'language-' + classArr[1];
        $this.addClass("line-numbers")
            .css("white-space", "pre-wrap");
        $this.children('code').addClass(lanClass);

        //var pre_content = '<code class="' + lan_class + '">' + $(this).children('code').html() + '</code>';
        //$(this).html(pre_content);
    });
    (<any>window).Prism.highlightAll();//js代码中调用此方法
}

const safeSetContent = (editor: tinymce.Editor, content: string) => {
    if (editor.initialized) {
        editor.setContent(content);
    } else {
        window.setTimeout(() => {
            safeSetContent(editor, content);
        }, 2000);
    }
}

var tinyMceLoader: ITinyMceLoader = {
    init: (selector = "#tinyEditor", tinymceType = "basic",
        height = 440, width = "100%",
        otherConfig = null,
        onReady = null) => {

        let lineheightFormats = "1 1.25 1.4 1.5 1.75 1.8 2";
        if (tinymceType === 'full') {
            lineheightFormats = '1 1.25 1.4 1.5 1.6 1.75 1.8 2 3 4 5';
        }
        var config: any = {
            selector: selector,
            init_instance_callback: (editor: any) => {
                typeof onReady === 'function' && onReady();
                //editor.setContent($(selector).val());
                editor.on('blur',
                    (e: any) => {
                        $(selector).val(editor.getContent());
                    });
                //editor.execCommand("fontName", false, "Arial");
                //editor.execCommand("fontSize", false, "12pt");
            },
            language: 'zh_CN',
            language_url: cdn + '/libs/tinymce/5.2.0/langs/zh_CN.js',
            width: width,
            height: height,
            theme: 'silver',
            relative_urls: false,
            browser_spellcheck: false,
            fontsize_formats: "8pt 10pt 12pt 14pt 16pt 18pt 20pt 22pt 24pt 28pt 20pt 36pt",
            font_formats:
                "Times New Roman=times new roman,times;Arial=arial,helvetica,sans-serif;Arial Black=arial black,avant garde;微软雅黑='微软雅黑';宋体='宋体';黑体='黑体';仿宋='仿宋';楷体='楷体';隶书='隶书'",
            lineheight_formats: lineheightFormats,
            paste_enable_default_filters: true,
            //prism js language list
            codesample_languages: [
                { text: 'C#', value: 'csharp' },
                { text: 'HTML/XML', value: 'markup' },
                { text: 'JavaScript', value: 'javascript' },
                { text: 'TypeScript', value: 'ts' },
                { text: 'Scss', value: 'scss' },
                { text: 'Python', value: 'python' },
                { text: 'Java', value: 'java' },
                { text: 'Markdown', value: 'md' },
                { text: 'YAML', value: 'yml' },
                { text: 'JSON', value: 'json' },
                { text: 'SQL', value: 'sql' },
                { text: 'Bash', value: 'bash' },
                { text: 'C', value: 'c' },
                { text: 'C++', value: 'cpp' }
            ],
        };
        if (otherConfig) {
            $.extend(config, otherConfig);
        }
        $.extend(config, (<any>tinyMceConfig)[tinymceType]);
        tinymce.init(config);
    },


    setContent: (content: string, selector: string = "tinyEditor") => {
        safeSetContent(tinymce.get(selector), content);
    },

    getContent: (selector: string = "tinyEditor") => {
        let editor = tinymce.get(selector);
        if (editor.initialized) {
            return editor.getContent();
        } else {
            return null;
        }
    }

};

export default tinyMceLoader;